	module.exports = {"data":{"post":{"html":"<p>I've done it. Encouraged among other things by a persistent comment spammer, I've now converted this blog to <a href=\"http://wordpress.org/\">WordPress</a> 1.2. At the same time, my permalinks are now cool hyphenated-titles-like-<a href=\"http://weblog.burningbird.net/\">Shelley</a>-and-<a href=\"http://diveintomark.org/\">Mark</a>, so I must be cool now as well. Of course the old links are redirected... well, most of them anyway. And I wanted to redo the blogroll anyway, so that's missing for the moment. But otherwise things should pretty much look and work as before, unless you want to link a dozen times to a porn site in a comment. That won't work anymore.</p>","fields":{"title":"Converted","date":"31 May 2004","url":"https://papascott.de/2004-05-31-converted/"}}},"pathContext":{"slug":"/2004-05-31-converted/","prev":{"fields":{"title":"Pentecost Singers","date":"30 May 2004","slug":"/2004-05-30-pentecost-singers/"}},"next":{"fields":{"title":"German Top 50 Blogparade","date":"01 Jun 2004","slug":"/2004-06-01-german-top-50-blogparade/"}}}}

